<template>
    <div class="html">
        <div class="answers">
            <div class="searchtop">
                <ul class="search">
                    <li class="search1">
                        <input
                            type="text"
                            class="inpuit"
                            v-on:keyup.enter="search"
                            v-model="inpuit"
                        >
                        <button class="button" @click="search">搜索关键词</button>
                    </li>
                    <li class="renovation active" @click="getdata(-1)">全部</li>
                    <li class="renovation" @click="state(0)">装修前</li>
                    <li class="renovation" @click="state(1)">装修中</li>
                    <li class="renovation" @click="state(2)">装修后</li>
                    <li class="statement" @click="affirmingshow=true">申明</li>
                </ul>
            </div>
            <!-- 展示列 -->
            <div class="listtop">
                <div v-for="(item,index) in all" :key="index" class="list" @click="open(index)">
                    <div class="ask">
                        {{index+1}}问：
                        <span>{{item.question}}</span>
                        <!-- {{item.answer}} -->
                        <img src="@/assets/answers/bottom.png" alt class="askimg">
                    </div>
                    <el-collapse-transition>
                        <div class="solve transition-box" v-show="item.show">
                            <div class="solve1 transition-box">
                                答：
                                <span>{{item.answer}}</span>
                            </div>
                        </div>
                    </el-collapse-transition>
                </div>
            </div>
			<div class="affirmingtop" v-show="affirmingshow">
				 <div class="affirming" >
                <h1>申明</h1>
                <img src="@/assets/answers/exit.png" alt @click="affirmingshow=false">
				<p>{{affirmingtext}}</p>
            </div>
			</div>
           
        </div>
    </div>
</template>

<script>
import reuqest from "@/utils/request";
export default {
    data() {
        return {
            // (0装修前,1装修中,2装修后)
            all: "",
			inpuit: "",
			affirmingshow:false,
            affirmingtext:
                "申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问"
            // open:''
        };
    },
    mounted() {
        this.getdata(-1);
    },
    methods: {
        affirming() {},
        // 搜索
        search() {
            reuqest({
                url: "/api/getQuestionsAnswersList",
                method: "post",
                data: {
                    search: this.inpuit
                    // type: "0"
                }
            }).then(data => {
                // console.log(data.data.data);
                for (let i = 0; i < data.data.data.length; i++) {
                    data.data.data[i].show = false;
                    // console.log(data.data.data[i].show);
                }
                this.all = data.data.data;
            });
        },
        getdata(types) {
            this.clear(types);
            reuqest({
                url: "/api/getQuestionsAnswersList",
                method: "post",
                data: {
                    // search: "",
                    // type: "0"
                }
            }).then(data => {
                // console.log(data.data.data);
                for (let i = 0; i < data.data.data.length; i++) {
                    data.data.data[i].show = false;
                    // console.log(data.data.data[i].show);
                }
                this.all = data.data.data;
            });
        },
        state(types) {
            this.clear(types);
            reuqest({
                url: "/api/getQuestionsAnswersList",
                method: "post",
                data: {
                    // search: "",
                    type: types
                }
            }).then(data => {
                console.log(data.data.data);
                for (let i = 0; i < data.data.data.length; i++) {
                    data.data.data[i].show = false;
                    console.log(data.data.data[i].show);
                }
                this.all = data.data.data;
            });
        },
        // 展开和折叠
        open(index) {
            let dakai = document.querySelectorAll(".askimg");
            let ask = document.querySelectorAll(".ask");
            console.log(ask[index].background);

            // console.log(this.all[index].show);
            if (this.all[index].show == false) {
                // 清除所有打打开的
                for (let i = 0; i < this.all.length; i++) {
                    this.all[i].show = false;
                    let src = require("../../assets/answers/bottom.png");
                    dakai[i].src = src;
                    ask[i].style.background = "#f5f5f5";
                }
                this.all[index].show = true;
                ask[index].style.background = "#ffebf5";
                // console.log(dakai[index]);
                let src = require("../../assets/answers/top.png");
                dakai[index].src = src;
            } else {
                let src = require("../../assets/answers/bottom.png");
                dakai[index].src = src;
                this.all[index].show = false;
            }
        },
        clear(types) {
            // 清除箭头方向
            let dakai = document.querySelectorAll(".askimg");
            let renovation = document.querySelectorAll(".renovation");
            let ask = document.querySelectorAll(".ask");
            console.log(renovation);
            for (let i = 0; i < renovation.length; i++) {
                renovation[i].style.background = "#fff";
                renovation[i].style.color = "#000";
            }
            //  renovation[0].style.background = '#1fcf3e'
            for (let i = 0; i < this.all.length; i++) {
                this.all[i].show = false;
                let src = require("../../assets/answers/bottom.png");
                dakai[i].src = src;
                ask[i].style.background = "#f5f5f5";
                // renovation[i].style.background = '#fff'
            }
            console.log(types);
            types = types + 1;
            // 切换装修状态
            renovation[types].style.background = "#1fcf3e";
            renovation[types].style.color = "#fff";
        }
    }
};
</script>

<style scoped>
.html {
    transition: all 0.5s;
    background: #e6e6e6;
}
.answers {
    display: inline-block;
    width: 1200px;
    background: white;
    margin: 0 auto;
    padding: 20px 50px 20px;
}
/* 搜索框 */
.search1 {
    width: 512px;
    height: 42px;
    border: 3px solid #ff006c;
    border-radius: 6px;
    text-align: left;
}
.search li {
    float: left;
}
.search li:last-child {
    float: right;
}
.inpuit {
    display: inline-block;
    width: 408px;
    float: left;
    padding-left: 8px;
    height: 36px;
    border: none;
    text-align: left;
    font-size: 17px;
}
.button {
    display: inline-block;
    float: left;
    width: 98px;
    height: 36px;
    line-height: 32px;
    /* position: absolute; */
    background: #ff006c;
    /* background: url(../images/search_button.png) no-repeat center center; */
    color: #fff;
    font-size: 17px;
    border: 3px solid #ff006c;
    outline: none;
    cursor: pointer;
}
.searchtop {
    overflow: hidden;
    zoom: 1;
}
.renovation {
    border: 2px solid #1fcf3e;
    color: #333;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
    width: 87px;
    float: left;
    height: 36px;
    line-height: 34px;
    text-align: center;
    margin: 3px 8px;
    border-radius: 5px;
    background: #fff;
}
.active {
    color: #fff;
    background: #1fcf3e;
}
.renovation:hover {
    color: #000;
    background: #1fcf3e;
}
.statement {
    width: 87px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    color: #fff;
    /* background: url(../images/shenming.png) no-repeat center center; */
    font-size: 16px;
    margin-top: 3px;
    background: #ff006c;
    border-radius: 5px;
    float: right;
}
.statement:hover {
    background: #ff006aaf;
}
.listtop {
    margin-top: 20px;
}
.list {
    /* padding: 10px 40px 10px 14px; */
    color: #ff0066;
    text-align: left;
    font-size: 14px;
    /* margin-top: 20px; */
    margin-bottom: 10px;
}
.ask {
    padding: 10px 40px 10px 14px;
    border: 1px solid #c1c1c1;
    width: 1098px;
    height: 44px;
    background: #f5f5f5;
    /* margin-bottom: 10px; */
    cursor: pointer;
    position: relative;
    z-index: 0;
}
.ask:hover {
    background: #ffebf5;
}
.ask span {
    color: #000;
}
.askimg {
    /* width: 10px; */
    /* height: 1px; */
    position: absolute;
    /* z-index: -1; */
    right: 20px;
    top: 15px;
    /* width: 16p; */
}
.solve {
    /* display: inline-block; */
    position: relative;
    /* z-index: -1; */
    /* padding: 10px 40px 10px 14px; */

    /* height: 44px; */
    background: #fff;
    border: 1px solid #d7d7d7;
    border-top: 0;
    overflow: hidden;
    width: 1098px;
    /* height: 0px; */
}
.solve1 {
    margin: 10px 40px 10px 14px;
}
.solve span {
    color: #000;
}
/* 申明 */
.affirming{
	width: 600px;
	height: 360px;
	position: fixed;
	/* left: 50%; */
	left:  calc(50% - 300px);
	top:calc(50% - 180px);
	z-index: 10;
}
.affirming h1{
    background: #ff0066;
    padding: 15px 10px;
    font-size: 22px;
    text-align: center;
    color: #fff;
}
.affirming img{
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}
.affirming p{
	    padding: 25px 14px 10px 25px;
    overflow-y: scroll;
    height: 300px;
	font-size: 17px;
	    color: #131212;
	background: #fff;
}
.affirmingtop{
	position: fixed;
	left: 0;
	top: 0;
	z-index: 10;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
}
</style>



